<template>
  <div class='ui-overlay'>
    <div class='ui-panel'>
      <div class='ui-width-8 ui-height-10 color-dark ui-scroll-y'>
        <a v-for='channel in store.channels' href='#'>{{channel.name}}</a>
      </div>
    </div>
  </div>
</template>

<script>
import SelectionList from './SelectionList.vue'
import store from '../store'
import socket from '../socket'

export default {
  components: {SelectionList},

  data () {
    return { store }
  },

  mounted () {
    socket.requestChannels()
  }
}
</script>



